*,
*::before,
*::after {
	box-sizing: border-box;
}

/* 2. Remove default margin */
* {
	margin: 0;
}

body {
	/* 3. Add accessible line-height */
	line-height: 1.5;
	/* 4. Improve text rendering */
	-webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
	display: block;
	max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
	font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
	text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
	isolation: isolate;
}


/* PROJECT CSS */

body {
	background-color: #0a192f;
	color: #f8f9fa;
	font-family: Orbitron;
}

.main-container {
	display: flex;
	gap: 150px;
	margin: auto;
	justify-content: space-evenly;
}

.player-section {
	padding-top: 100px;
}

.game-board {
	display: flex;
	flex-direction: column;
	height: 400px;
	width: 400px;
	margin: 20px auto;
	border: 1px solid black;
}

.game-row {
	display: flex;
	flex: 1;
}

.game-cell {
	background-color: #1f2937;
	flex: 1;
	border: 1px solid #324b5e;

}

.game-cell:hover {
	background-color: #20c997;
}

.highlight-ship {
	background-color: #6c757d;
}

.highlight-ship-attacked {
	background-color: #dc3545;
}

.highlight-missed-shot {
	background-color: #5bc0de;
}

.highlight-ship-sunk {
	background-color: #8b0000;
}

.player-section {
	padding-top: 20px;
}

.game-title {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 4rem;
	margin-bottom: 2rem;
}

.game-title img {
	max-width: 600px;
	width: 100%;
	height: auto;
	object-fit: contain;
	filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.4));
}

.dialog {
	background: rgba(20, 30, 50, 0.95);
	border: 2px solid #00bfff;
	border-radius: 12px;
	padding: 2rem;
	max-width: 400px;
	width: 90%;
	margin: 100px auto;
	box-shadow: 0 0 20px #00bfff99;
	text-align: center;
	font-family: 'Orbitron', sans-serif;
	color: #cce7ff;
}

.dialog h2 {
	margin-bottom: 1.5rem;
	font-size: 1.6rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	color: #00bfff;
}

.dialog input {
	width: 100%;
	padding: 0.75rem;
	margin-bottom: 1.5rem;
	border: 1px solid #00bfff;
	border-radius: 8px;
	font-size: 1rem;
	background-color: #0a1a2f;
	color: #cce7ff;
}

.dialog input::placeholder {
	color: #5fa9d1;
}

.dialog button {
	padding: 0.75rem 1.5rem;
	background-color: #00bfff;
	color: #0a1a2f;
	border: none;
	border-radius: 8px;
	font-size: 1rem;
	font-weight: bold;
	cursor: pointer;
	transition: background-color 0.3s ease;
}

.dialog button:hover {
	background-color: #0099cc;
}

.ship-dialog {
	background-color: #0c1b2a;
	color: #ffffff;
	padding: 2rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	gap: 2rem;
	font-family: 'Orbitron', sans-serif;
}

.ship-dialog h2 {
	font-size: 1.8rem;
}

.ship-placement-container {
	display: flex;
	gap: 3rem;
	justify-content: center;
	align-items: flex-start;
	flex-wrap: wrap;
}

.ship-list {
	display: flex;
	flex-direction: column;
	gap: 1rem;
	border-left: 2px solid #2d3c4f;
	padding-left: 2rem;
	min-width: 150px;
}

.ship-item {
	background-color: #7a8a93;
	color: #0c1b2a;
	font-weight: bold;
	padding: 0.5rem 1rem;
	border-radius: 6px;
	text-align: center;
	cursor: pointer;
	transition: background-color 0.2s ease;
}

.ship-item:hover {
	background-color: #b0bec5;
}

.start-btn {
	margin-top: 2rem;
	padding: 0.8rem 2rem;
	font-size: 1rem;
	font-weight: bold;
	background-color: #2196f3;
	color: white;
	border: none;
	border-radius: 6px;
	cursor: pointer;
	transition: background-color 0.2s ease;
}

.start-btn:hover {
	background-color: #1976d2;
}

.orientation-btn {
	padding: 6px 12px;
	background-color: #34495e;
	color: #ecf0f1;
	border: none;
	border-radius: 4px;
	cursor: pointer;
